:root {
  --font-family: "Onest", sans-serif;
  --color-gray-100: #F2F2F2;
  --color-gray-150: rgba(0, 0, 0, 0.15);
  --color-gray-200: #F0F0F0;
  --color-gray-300: #EAEAEA;
  --color-gray-400: rgba(0, 0, 0, 0.4);
  --color-black: #000000;
  --color-white: #FFFFFF;
}

.flex {
  display: flex;
}

.flex-between {
  justify-content: space-between;
}

.flex-center {
  justify-content: center;
}

.items-center {
  align-items: center;
}

.button {
  display: flex;
  height: 48px;
  padding: 13px 25px;
  text-align: center;
  justify-content: center;
  align-items: center;
}

.button--primary {
  background-color: var(--color-black);
  color: var(--color-white);
  font-weight: 500;
  border-radius: 25px;
}

.button--outlined {
  color: var(--color-black);
  border: 1px solid var(--color-black);
  border-radius: 25px;
}

.link--primary {
  width: 45px;
  height: 45px;
  background-color: var(--color-black);
  border-radius: 100%;
  display: flex;
}

.w-100 {
  width: 100%;
}

.divider {
  height: 1px;
  background-color: var(--color-gray-150);
}

.list li {
  padding-left: 20px;
  position: relative;
}
.list li::before {
  content: "";
  display: inline-block;
  width: 10px;
  height: 10px;
  background-color: var(--color-black);
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 3px;
}

.inpt {
  border: 1px solid rgba(0, 0, 0, 0.25);
  border-radius: 10px;
  height: 35px;
  padding: 0 15px;
}
.inpt::placeholder {
  color: rgba(0, 0, 0, 0.5);
}

.title {
  font-size: 32px;
  font-weight: 900;
}

.text-center {
  text-align: center;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

h1, .h1 {
  font-size: 62px;
  font-weight: 700;
}

h2, .h2 {
  font-size: 32px;
  font-weight: 700;
}

h3, .h3 {
  font-size: 22px;
  font-weight: 600;
}

h4, .h4 {
  font-size: 18px;
  font-weight: 700;
}

.color--shaded {
  color: rgba(0, 0, 0, 0.5);
}

.color--black {
  color: var(--color-black);
}

.font-bold {
  font-weight: 700;
}

.right-oriented {
  padding: 50px 0;
}
.right-oriented__wrapper {
  margin-top: 35px;
}
.right-oriented__descr {
  width: 51%;
}
.right-oriented__descr p:nth-child(1) {
  margin-bottom: 25px;
}
.right-oriented .h2 {
  width: 34%;
}
.right-oriented .h3 {
  width: 40%;
}
.right-oriented ul.gap-25 {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.right-oriented li {
  padding-left: 15px;
  position: relative;
}
.right-oriented li::before {
  content: "";
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: var(--color-gray-400);
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 7px;
}
.right-oriented.larger-title .h2 {
  width: 38%;
}
.right-oriented.larger-title .h3 {
  width: 45%;
}
.right-oriented .descr-subtitle {
  font-size: 18px;
  margin-bottom: 25px;
}

.left-oriented {
  padding: 50px 0;
}
.left-oriented__wrapper {
  margin-top: 35px;
}
.left-oriented__descr {
  width: 51%;
}
.left-oriented .h2 {
  width: 34%;
}
.left-oriented .h3 {
  width: 40%;
}
.left-oriented.larger-title .h2 {
  width: 38%;
}
.left-oriented ul.gap-25 {
  display: flex;
  flex-direction: column;
  gap: 25px;
}
.left-oriented li {
  padding-left: 15px;
  position: relative;
}
.left-oriented li::before {
  content: "";
  display: inline-block;
  width: 5px;
  height: 5px;
  background-color: var(--color-gray-400);
  border-radius: 100%;
  position: absolute;
  left: 0;
  top: 7px;
}

@media screen and (max-width: 1199.99px) {
  .button {
    padding: 13px 15px;
  }
}
@media screen and (max-width: 1024.99px) {
  .title {
    font-size: 48px;
  }
  .h1, h1 {
    font-size: 48px;
  }
}
@media screen and (max-width: 767.99px) {
  .title {
    font-size: 42px;
  }
  .h1 {
    font-size: 42px;
  }
  .right-oriented__wrapper {
    flex-wrap: wrap;
    text-align: center;
  }
  .right-oriented__descr {
    width: 100%;
    margin-top: 35px;
  }
  .right-oriented .h3 {
    width: 100%;
  }
  .right-oriented .principe__descr {
    width: 100%;
    margin-top: 50px;
  }
  .right-oriented.larger-title .h3 {
    width: 100%;
  }
  .right-oriented ul.descr-item, .right-oriented .descr-item.text-left {
    text-align: left;
  }
}
* {
  box-sizing: border-box;
}

html, body {
  overflow-x: hidden;
}

.spacer {
  display: block;
  width: 100%;
}
.spacer--40 {
  height: 40px;
}
.spacer--72 {
  height: 72px;
}
.spacer--80 {
  height: 80px;
}
.spacer--120 {
  height: 120px;
}
.spacer--150 {
  height: 150px;
}
.spacer--200 {
  height: 200px;
}

body {
  font-family: var(--font-family);
  font-size: 14px;
  padding: 0;
  margin: 0;
}

.container {
  width: 100%;
  max-width: 1524px;
  padding-left: 100px;
  padding-right: 100px;
  margin: 0 auto;
}
.container.container-sm {
  max-width: 1200px;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
  transition: all 0.2s linear;
}

p {
  margin: 0;
  padding: 0;
}

img, svg, video {
  max-width: 100%;
  max-height: 100%;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
  background: none;
  padding: 0;
  font-family: inherit;
}

@media screen and (max-width: 1199.99px) {
  .container {
    padding-left: 70px;
    padding-right: 70px;
  }
}
@media screen and (max-width: 1024.99px) {
  .container {
    padding-left: 40px;
    padding-right: 40px;
  }
}
@media screen and (max-width: 640.99px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
.header {
  padding: 20px 0;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  background-color: var(--color-gray-100);
  z-index: 3;
  transition: all 0.37s cubic-bezier(0, 0, 0.58, 1);
}
.header.active {
  padding: 8px 0;
  box-shadow: 0 1px 10px rgba(0, 0, 0, 0.15);
  position: fixed;
}
.header__wrapper {
  width: 100%;
  height: 100%;
  gap: 100px;
}
.header__inner {
  display: flex;
  justify-content: space-between;
  flex: 1;
}
.header__logo {
  font-size: 20px;
  font-weight: 700;
  white-space: nowrap;
  background-color: var(--color-white);
  padding: 10px 20px;
  border-radius: 100px;
}
.header__nav li {
  padding: 10px 20px;
  border-radius: 25px;
  transition: background-color 0.2s linear;
}
@media (hover: hover) {
  .header__nav li:hover {
    background-color: var(--color-gray-100);
  }
}
.header__actions {
  display: flex;
  gap: 15px;
}
.header .divider {
  margin-top: 15px;
}

.burger {
  width: 20px;
  height: 16px;
  position: relative;
}

.burger .bottom-line,
.burger .middle-line,
.burger .top-line {
  position: absolute;
  left: 0;
  right: 0;
  width: 20px;
  height: 2px;
  background-color: #000;
}

.burger .top-line {
  top: 0px;
  transition: all 0.2s ease-in-out;
}

.burger .middle-line {
  top: 8px;
  transition: all 0.2s ease-in-out;
}

.burger .bottom-line {
  top: 16px;
  transition: all 0.2s ease-in-out;
}

.open .top-line {
  transform: translate3d(0, 6px, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(45deg) skew(0deg, 0deg);
  transform-style: preserve-3d;
  transition: all 0.3s ease-in-out;
}

.open .middle-line {
  opacity: 0;
}

.open .bottom-line {
  transition: all 0.3s ease-in-out;
  transform: translate3d(0, -10px, 0) scale3d(1, 1, 1) rotateX(0) rotateY(0) rotateZ(-45deg) skew(0deg, 0deg);
  transform-style: preserve-3d;
}

.burger {
  display: none;
}

@media screen and (max-width: 1199.99px) {
  .header__actions {
    gap: 10px;
  }
  .header__nav li {
    padding: 10px;
  }
}
@media screen and (max-width: 1024.99px) {
  .header {
    position: relative;
    padding: 10px 0;
  }
  .header__nav-outer {
    position: absolute;
    background-color: var(--color--white);
    flex-direction: column;
    width: fit-content;
    padding: 30px;
    height: 100vh;
    top: 65px;
    right: -100%;
    box-shadow: -2px 5px 10px rgba(0, 0, 0, 0.1);
    transition: all 0.4s cubic-bezier(0.8, 0.1, 0.01, 0.04);
    background-color: #fff;
    min-width: 320px;
    z-index: 9;
  }
  .header__nav-outer.active {
    right: 0;
  }
  .header__nav ul {
    flex-direction: column;
  }
  .burger {
    display: block;
  }
  .header__wrapper {
    justify-content: space-between;
    gap: unset;
  }
  .header__inner {
    flex: initial;
    gap: 16px;
    align-items: center;
  }
  .link--primary {
    width: 32px;
    height: 32px;
  }
}
@media screen and (max-width: 600.99px) {
  .header__logo {
    font-size: 16px;
  }
  .link--primary {
    width: 28px;
    height: 28px;
  }
  .header__actions {
    gap: 8px;
  }
  .header__logo {
    padding: 10px 14px;
  }
}
.hero {
  padding-top: 100px;
  padding-bottom: 50px;
}
.hero__wrapper {
  margin-top: 60px;
}
.hero__image {
  height: 605px;
  width: 50%;
}
.hero__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.hero__info {
  width: calc(50% - 150px);
}
.hero .descr {
  margin-top: 15px;
}
.hero__cta {
  display: flex;
  gap: 10px;
  margin-top: 50px;
}
.hero__cta a {
  flex: 1;
}
.hero .h3 {
  max-width: 65%;
  margin-top: 25px;
}

@media screen and (max-width: 1199.99px) {
  .hero__image {
    height: auto;
    width: 46%;
  }
  .hero__info {
    width: calc(50% - 20px);
  }
}
@media screen and (max-width: 1024.99px) {
  .hero {
    padding-top: 20px;
  }
}
@media screen and (max-width: 767.99px) {
  .hero .h3 {
    width: 100%;
    max-width: 100%;
  }
  .hero__wrapper {
    flex-wrap: wrap;
  }
  .hero__image {
    width: 100%;
  }
  .hero__info {
    width: 100%;
    text-align: center;
    margin-top: 50px;
  }
}
@media screen and (max-width: 600.99px) {
  .hero__wrapper {
    margin-top: 20px;
  }
  .hero__cta {
    flex-wrap: wrap;
  }
  .hero__cta a {
    flex: 1 1 100%;
  }
}
/* === coffeeshop === */
.brand {
  padding: 75px 0;
}
.brand__wrapper {
  margin-top: 35px;
}
.brand__descr {
  width: 51%;
}
.brand .h2 {
  width: 34%;
}

@media screen and (max-width: 1199.99px) {
  .brand__descr {
    width: 100%;
  }
}
.menu {
  padding: 95px 0;
  background-color: var(--color-gray-300);
  margin-bottom: 50px;
}
.menu__heading {
  display: flex;
  justify-content: space-between;
}
.menu__wrapper {
  display: flex;
  margin-top: 50px;
}
.menu__card {
  width: calc(25% - 15px);
}
.menu__card-img {
  width: 100%;
  height: 265px;
  background-color: var(--color-white);
  border-radius: 35px 35px 0 0;
}
.menu__card-title {
  margin-top: 25px;
  font-weight: bold;
}
.menu__card-descr {
  margin-top: 15px;
}
.menu__navigation {
  justify-content: center;
  margin-top: 35px;
  gap: 10px;
}
.menu__navigation .swiper-button {
  position: static;
}
.menu__navigation .swiper-button::after {
  display: none;
}

@media screen and (max-width: 767.99px) {
  .menu {
    padding: 65px 0;
  }
}
.coffee-slider {
  padding: 75px 0;
  background-color: var(--color-gray-300);
}
.coffee-slider__wrapper {
  display: flex;
}
.coffee-slider__card {
  width: calc(25% - 15px);
}
.coffee-slider__card-img {
  width: 100%;
  height: 350px;
  background-color: var(--color-white);
  display: flex;
  border-radius: 35px 35px 0 0;
}